.button {
  border: 1px solid transparent;
  border-radius: 3px;
  padding: 5px 24px;
  color: #ffffff;
  font-family: var(--basic-font-family);
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  cursor: pointer;
}

.button + .button {
  margin-left: 24px;
}

.disabled {
  opacity: 0.5;
  color: #002E5C;
  background: #004D99;
}

.secondary {
  background-color: transparent;
  border-color: #0066CC;
  color: #0066CC;
}

.default {
  background-color: #0066CC;
}

.red {
  background-color: #CC1439;
}

.default:hover {
  background-color: #0080FF;
}

.red:hover {
  background-color: #FF1A47;
}

.secondary:hover {
  background-color: #E5F0FA;
}

.default:active {
  background-color: #0059B2;
}

.secondary:active {
  background-color: #C9E5FF;
}

.red:active {
  background-color: #B21232;
}

.button:disabled {
  cursor: default;
}
